.top_menu.navbar.top_menu.navbar{
	padding: 0;
    position: absolute;
    top: 0;
    width: 100%;
	z-index: 1;
}
.top_menu .logo{
	position: absolute;
    top: 40px;
    left: 61px;
}
.top_menu .logo img{
	vertical-align: top;
	width: 156px;
}
.top_menu ul{
	padding: 0 76px;
    width: 100%;
}
.top_menu li{
    list-style: none;
}
.top_menu a,
.top_menue a:visited{
	color: #707070;
	font-size: 14px;
	letter-spacing: 1px;
	line-height: 120px;
	font-family: 'NHaasGroteskTXPro-Rg';
}
.top_menu a:hover,
.top_menu a:active{
	color: #dc0000;
}
.top_menu li.active a,
.top_menu li:hover a{
	color: #dc0000;
	position: relative;
}
.top_menu li.active a::before{
    content: "";
    position: absolute;
    top: -14px;
    left: 32px;
    right: 0;
    margin: 0 auto;
    width: 1px;
    height: 16px;
    background-color: #dc0000;
    transform: rotate(48deg);
	animation-name: hover_before;
	animation-fill-mode: forwards;
	animation-duration: 0.5s;
}
.top_menu li.active a::after{
	content: "";
	position: absolute;
	bottom: -14px;
	left: -33px;
	right: 0;
	margin: 0 auto;
	width: 1px;
	height: 15px;
	background-color: #dc0000;
	transform: rotate(48deg);
	animation-name: hover_after;
	animation-fill-mode: forwards;
	animation-duration: 0.5s;
}

.top_menu li:hover a::before{
	content: "";
	position: absolute;
	top: 14px;
	left: -32px;
	right: 0;
	margin: 0 auto;
	width: 1px;
	height: 16px;
	background-color: #dc0000;
	transform: rotate(48deg);
	animation-name: hover_before;
	animation-fill-mode: forwards;
	animation-duration: 0.5s;
}

.top_menu li:hover a::after{
	content: "";
	position: absolute;
	bottom: 14px;
	left: 33px;
	right: 0;
	margin: 0 auto;
	width: 1px;
	height: 15px;
	background-color: #dc0000;
	transform: rotate(48deg);
	animation-name: hover_after;
	animation-fill-mode: forwards;
	animation-duration: 0.5s;
}

.top_menu .navbar-toggler{
	border: 0;
	outline: 0;
}

@keyframes hover_before {
	0%{
		top: 14px;
		left: -32px;
	}

	100%{
		top: -14px;
		left: 32px;
	}
}
@keyframes hover_after {
	0%{
		bottom: 14px;
		left: 33px;
	}

	100%{
		bottom: -14px;
		left: -33px;
	}
}

@media all and (max-width: 1400px) {
	.top_menu ul{
    	padding: 0 140px;
	}	
}/*end 1400px*/

@media all and (max-width: 1200px) {
	.top_menu .logo {
	    position: absolute;
	    top: 40px;
	    left: 30px;
	}
	.top_menu ul{
    	padding: 0 150px;
	}		
}/*end 1200px*/

@media all and (max-width: 1100px) {
	.top_menu .logo {
	    position: absolute;
	    top: 40px;
	    left: 30px;
	}	
}/*end 1100px*/

@media all and (max-width: 991px) {
	.top_menu .logo{
		position: static;
	}
	.top_menu ul{
		flex-direction: column;
	    padding: 0;
	    text-align: center;
		margin-bottom: 0;
	}
	.top_menu a, .top_menue a:visited{
		line-height: 40px;
	}
	.top_menu.navbar.top_menu.navbar {
	    padding: 20px;
		z-index: 10;
	}
	.navbar-collapse{
		background-color: #fff;
	}
}/*end 1100px*/

@media all and (max-width: 768px) {
		
}/*end 768px*/